*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    font-family: Helvetica, Arial, sans-serif;
    padding: 60px;
    background-color: #eee;
}

.app-title{
    margin-bottom: 10px;
    color: darkolivegreen;
}

input{
    color: #333;
    font-size: 1.1rem;
    width: 100%;
    padding: 5px 10px;
    border: 1px solid #333;
    border-radius: 5px;
}

input:focus{
    outline: none;
    border: 1px solid darkolivegreen;
    box-shadow: 0 0 1px 1px rgba(85, 107, 47, 0.9);
}

.container{
    width: 100%;
    margin: auto;
    margin-top: 10px;
}

.container h3{
    text-align: center;
    color: darkolivegreen;
}


.card-grid{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
}

.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    overflow: hidden;
    margin: 6px;
    width: 230px;
    display: flex;
    flex-direction: column;
}

.card p{
    text-align: center;
    font-size: 0.7rem;
    width: 100%;
    padding: 8px;
}

.card img{
    width: 100%;
    height: 170px;
}